

.main-page {
     position: relative;

     @keyframes start-shine { 
          from { box-shadow: 0px 0px 0px 0px #FBFF22; }   
          to { box-shadow: 0px 0px 30px 10px #FBFF22; } 
     }

     &__gift-icon {
          z-index: 10;
          position: fixed;
          padding: 5px;
          right: 20px;
          bottom: 10px;
          background-color: #FBFF22;
          border-radius:50%;
          cursor:pointer;
          animation: start-shine 2s ease-in-out infinite alternate;

          @media (max-width: 590px) {
               width: 40px;
          };
     }
     &__row {
          margin: 3.5em 0;
          display: flex;
          align-items: center;
          gap: 2em;


          @media (max-width:1120px) {
               align-items: start;

          }

          @media (max-width:695px) {
               flex-direction: column-reverse;
               align-items: center;
               margin: 1.2em;
          }
     }

     &__my-project-title {
          width: 100%;
          position: relative;
          text-align: center;

          svg {
               width: 100%;

               @media (max-width:690px) {
                    height: 100px;
               }

               @media (max-width:520px) {
                    height: 70px;
               }
          }

          &::after {
               content: "MY PROJECTS";
               display: inline-block;
               position: absolute;
               left: 50%;
               top: 50%;
               transform: translate(-50%, -50%);
               text-transform: uppercase;
               white-space: nowrap;
               font-weight: 600;
               font-size: 40px;
               color: #FBFF22;
               opacity: 0.3;
               transition: all 1.3s ease 0s;

               @media (max-width:690px) {
                    font-size: 20px;
               }
          }

          @media (min-width: 750px) {
               &:hover {
                    &::after {
                         opacity: 1;
                    }
               }
          }

          @media (max-width: 750px) {
               &:active {
                    &::after {
                         opacity: 1;
                    }
               }
          };
     }

     @media (max-width:1120px) {
          font-size: 10px;
     }
}

//==================================================================================

.projects-block {
     margin: 1.25em 0px;
     // width: 100%;

     &__container {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 1em 0;
     }

     &__svg-wrapper {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          position: relative;
          width: 100%;

               span  {
                    display: inline-block;   
                    text-align: center;
                    position: absolute;
                    top: 20%;
                    opacity: 0.2;
                    font-size: 22px;
                    transition: opacity 2.3s ease 0s;
                    pointer-events: all;
     
                    @media (max-width:750px) {
                         font-size: 25px;
                    };
     
                    @media (max-width: 500px) {
                         font-size: 20px;
                    };

                    b {
                         color: #FBFF22;
                    }
               };
          }

     &__actions-block {
          position: relative;
          width: 100%;
          margin: 2.6em 0px 0px 0px;
          display: flex;
          flex-direction: column;
          align-items: center;

          & .btn-yellow  {
               align-self: center;
          }

          svg {
               width: 100%;
               height: 200px;

               @media (min-width:630px) {
               };

               @media (max-width:500px) {
                    height: 100px;
                    margin-top: 10px;
               };
          }

          @media (min-width: 750px) {
               &:hover {
                    &::after {
                         opacity: 1;
                    }
               }
          }

          @media (max-width: 750px) {
               &:active {
                    &::after {
                         opacity: 1;
                    }
               }
          };

          @media (min-width:750px) {
               font-size: 50px;
          };
     }
}
