@import "_variables";
//<>====================================================================================================================================
@charset "UTF-8";

* {
     padding: 0;
     margin: 0;
     border: 0;
}

*,
*:before,
*:after {
     -webkit-box-sizing: border-box;
     box-sizing: border-box;
}

:focus,
:active {
     outline: none;
}

a:focus,
a:active {
     outline: none;
}

nav,
footer,
header,
aside {
     display: block;
}

html,
body {
     height: 100%;
     width: 100%;
     font-family: $font-family;
     font-size: 100%;
     line-height: 1;
     font-size: 14px;
     -ms-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
     -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
     font-family: $font-family;
}


input::-ms-clear {
     display: none;
}

button {
     background-color: transparent;
     color: inherit;
     font-size: inherit;
     font-weight: inherit;
     cursor: pointer;
}

button::-moz-focus-inner {
     padding: 0;
     border: 0;
}

a,
a:visited {
     text-decoration: none;
}

a:hover {
     text-decoration: none;
}

ul li {
     list-style: none;
}

img {
     vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
     font-size: inherit;
     font-weight: inherit;
}

//<>====================================================================================================================================